@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
body {
	margin: 0;
	font-family: "Ubuntu Mono", monospace;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgba(24, 34, 45);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: left;
  color: white;
  font-size: 200%;
  /* font-size: 500%; - for the really zoomed in version*/
}
.tickerline {
	padding: 0px;
}

.navbar {
	padding-left: 5px;
	padding-bottom: 5px;
}

.card {
	background-color: transparent;
	border-width: 1px;
	border-color: transparent;
}

.card-body {
	background-color: transparent;
	padding: 0.1rem;
}

canvas {
	padding-top: 4px;
}

hr {
	max-width: 100%;
	width: auto;
	margin: 0%;
	background-color: rgb(128, 128, 128);
}

.tickerContainer{
  padding-left: 18px;
  padding-right: 18px;
}
#ticker-bar {
	background-color: rgba(24, 34, 45);
	font-size: 100%;
	/* font-size: 100%;- for the really zoomed in version*/
}

.navbar {
	background-color: rgba(24, 34, 45);
}

.form-control {
	background-color: transparent;
	border: transparent;
	outline-width: 0px;
}

.form-control::-webkit-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: white;
}

.form-control:-ms-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: white;
}

.form-control::placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: white;
}
.form-control:focus::-webkit-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	background: linear-gradient(
		330deg,
		#e05252 0%,
		#99e052 25%,
		#52e0e0 50%,
		#9952e0 75%,
		#e05252 100%
	);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: bolder;
}
.form-control:focus:-ms-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	background: linear-gradient(
		330deg,
		#e05252 0%,
		#99e052 25%,
		#52e0e0 50%,
		#9952e0 75%,
		#e05252 100%
	);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: bolder;
}
.form-control:focus::placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	background: linear-gradient(
		330deg,
		#e05252 0%,
		#99e052 25%,
		#52e0e0 50%,
		#9952e0 75%,
		#e05252 100%
	);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: bolder;
}

.form-control:focus {
	background-color: transparent;
	color: white;
	box-shadow: none;
}

.clock {
	padding-right: 4.4rem;
}

img{
    vertical-align: middle;
}
